<template>
    <div>
        <div class="ticket-cell">
            <div v-if="this.role.toString()=='1'" class="qcode-div">
                <i v-show="goods.state=='0'" class="iconfont icon-qcode"></i>
                <i v-show="goods.state=='1'" class="iconfont icon-yishiyong1"></i>
                <i v-show="goods.state=='2'" class="iconfont icon-yiguoqi"></i>
            </div>
            <div class="cell-info">
                <div class="cell-info-left">
                    <div class="img-container">
                        <img v-lazy="goods.url" class="img" />
                    </div>
                </div>
                <div class="cell-info-right">
                    <p class="name">{{goods.names}}</p>
                    <p class="dec">{{goods.dec}}</p>
                    <p v-show="goods.state=='0'" class="valid-time">有效期:{{goods.starttm}}至{{goods.endtm}}</p>
                    <p v-show="goods.state=='1'" class="valid-time">使用于:{{goods.usetm}}</p>
                    <p v-show="goods.state=='2'" class="valid-time">过期时间:{{goods.endtm}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "ticket-cell",
  data() {
    return {};
  },
  props: {
    goods: {
      type: Object,
      default: function() {
        return {
          url: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          names: "380美容套餐",
          dec: "",
          starttm: "2018-01-20",
          endtm: "2018-02-20",
          price: 333,
          usetm:"",
          state:""
        };
      }
    }
  },
  components: {},
  activated() {},
  created() {},
  computed: {
    ...mapState(["role"])
  },
  methods: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.ticket-cell {
    width: 100%;
    position: relative;

    .qcode-div {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);

        .icon-qcode {
            font-size: 30px;
            color: #42495C;
        }
        .icon-yishiyong1{
            color: #8a8a8a;
            font-size: 48px;
        }
        .icon-yiguoqi{
            color: #999;
            font-size: 50px;
        }
    }

    .cell-info {
        font-size: 0px;
        background-color: #fff;
        display: flex;
        padding: 13px 0;
        align-items: center;

        .cell-info-left {
            flex: 0 0 74px;
            text-align: center;

            .img-container {
                width: 56px;
                height: 56px;
                display: inline-block;

                .img {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    border-radius: 3px;
                }
            }
        }

        .cell-info-right {
            flex 1;

            .name {
                font-size: 15px;
                font-weight: 700;
                color: #42495c;
            }

            .dec {
                padding: 4px 0px 2px 0px;
                font-size: 13px;
                color: #42495c;
                width: 170px;
                line-height 15px;
            }

            .valid-time {
                font-size: 13px;
                color: #acacac;
            }
        }
    }
}
</style>



