<template>
    <div class="ticket-list">
        <ticket-cell class="ticket-cell" v-for="(dataItem, index) in goodsArr" :cellindex="index" :goods="dataItem" @click.native="cellClickHandle"></ticket-cell>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { formatDate } from "../../../common/js/date.js";
import ticketCell from "../../public/ticketCell.vue";

export default {
  name: "ticket-list",
  data() {
    return {
      mealId: "",
      goodsArr: []
    };
  },
  components: {
    ticketCell
  },
  mounted() {
    document.body.scrollTop = 0;
    this.mealId = this.$route.params.lookMealId;
    this.getTicketsData();
  },
  methods: {
    cellClickHandle(event) {
      if (this.role.toString() != "1") return;
      let curTarget = event.currentTarget;
      let cellIndex = curTarget.getAttribute("cellindex");
      let dataItem = this.goodsArr[cellIndex];
      if (dataItem.state.toString()!="0") return;
      this.$router.push({
        name: "ticketQcode",
        params: {
          mealId: this.mealId,
          id: dataItem.id,
          name: dataItem.names,
          imgUrl: dataItem.url
        }
      });
    },
    getTicketsData() {
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "couponlist",
          buyid: this.mealId
        })
        .then(response => {
          let data = response.body;
          let ticketsArr = [];
          data.arr.forEach((val, index, arr) => {
            let tempDate = new Date(val.datefrom * 1000);
            let buyTime = formatDate(tempDate, "yyyy-MM-dd");
            tempDate = new Date(val.dateto * 1000);
            let endTime = formatDate(tempDate, "yyyy-MM-dd");
            let usetime = "";
            if (val.state.toString() == "1") {
              tempDate = new Date(val.consumedate * 1000);
              usetime = formatDate(tempDate, "yyyy-MM-dd");
            }
            ticketsArr.push({
              id: val.cpid,
              names: val.cname,
              url: this.path + val.iconurl,
              starttm: buyTime,
              endtm: endTime,
              dec: val.message,
              state:val.state.toString(),          //0代表未使用 1代表已使用 2代表已过期
              usetm: usetime
            });
          });
          this.goodsArr = ticketsArr;
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    }
  },
  computed: {
    ...mapState(["path", "userid", "role"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.ticket-list {
  width: 100%;
  padding-top: 0.3rem;

  .ticket-cell {
    margin-bottom: 0.07rem;
    box-shadow: 0 1px 0px rgba(66, 73, 92, 0.2);
  }
}
</style>



